.text {
    max-width: 800px;
    margin: 0 auto;
    display: block;
    text-align: center;
  }
  
  .mobile-nav-button {
    width: 35px;
    position: relative;
    /* margin: 2rem; */
    right: 0;
    top: 0;
    z-index: 9999;
    cursor: pointer;
    width: 35px;
    height: 30px;
  }
  .mobile-nav-button .open {

  }
  .mobile-nav-button .mobile-nav-button__line {
    width: 100%;
    height: 2px;
    background: #0e0e0e;
    position: relative;
    transition: 1s ease;
  }
  .mobile-nav-button .ls {
    width: 80%;
    /* clip: rect(1px); */
    /* transform: rotate(90deg) */
  }
  .mobile-nav-button .mobile-nav-button__line:nth-of-type(2) {
    margin: 0.5rem 0;
  }
  .mobile-nav-button .mobile-nav-button__line--1 {
    transform: rotate(45deg);
    top: 13px;
    position: absolute;
  }
  .mobile-nav-button .mobile-nav-button__line--3 {
    display: none;
  }
  .mobile-nav-button .mobile-nav-button__line--2 {
    transform: rotate(135deg);
    top: 5px;
    position: absolute;
  }
  
  .mobile-menu {
    display: block;
    max-width: 400px;
    width: 100%;
    right: 0;
    height: 100vh;
    background: #fff;
    /* border: 1px #000 solid; */
    box-shadow: 1px 1px 5px 2px;
    position: absolute;
    z-index: 9998;
    transition: 0.6s ease;
    top: 0;
    /* opacity: 0; */
  }
  .mobile-menu ul {
    display: grid;
    justify-content: end;
    justify-items: end;
    gap: 25px;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    padding: 0 10px 0 0;

  }
  .mobile-menu ul li {
    list-style: none;
    text-decoration: underline 2px;
  }
  .mobile-menu ul li a {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: block;
    text-align: center;
    text-decoration: none;
    color: #0e0e0e;
    font-size: 3rem;
    font-weight: 400;
    overflow: hidden;
    position: relative;
  }
  .mobile-menu ul li a:after {
    content: "";
    background: #0e0e0e;
    width: 100%;
    height: 100%;
    position: absolute;
    right: -100%;
    top: 0;
    z-index: -1;
    transition: 0.4s ease;
  }
  .mobile-menu ul li a:hover {
    color: #fff;
  }
  .mobile-menu ul li a:hover:after {
    right: 0;
  }

  .mobile-menu img{
    display: none;
  }

